*, *:before, *:after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  border: 0;
}

html, body {
  height: 100%;
  line-height: 1;
}

body {
  font-family: Rubik, sans-serif;
  font-size: 12px;
  color: #616161;
}

a {
  text-decoration: none;
}

ol, ul, li {
  list-style: none;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: inherit;
  font-size: inherit;
}

[class*=__container] {
  max-width: 1057px;
  margin: 0 auto;
}

.wrapper {
  display: flex;
  overflow: hidden;
  min-height: 100%;
  flex-direction: column;
}